<template>
  <div class="login">
    <div class="title">
      登录
      <span class="right" @click="$router.push('/register')">注册</span>
    </div>
    <van-form @submit="onSubmit">
      <van-field
        v-model="user.phone"
        name="账号"
        label="账号"
        placeholder="输入账号"
        :rules="[{ required: true, message: '请填写用户名' }]"
      />
      <van-field
        v-model="user.password"
        type="password"
        name="密码"
        label="密码"
        placeholder="密码"
        :rules="[{ required: true, message: '请填写密码' }]"
      />
      <div style="margin: 16px;">
        <van-button round block type="info" native-type="submit">登录</van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import { reqLogin } from "../../utils/http.js";
import { Toast } from "vant";
export default {
  mounted(){
  
  },
  methods: {
    onSubmit() {
      reqLogin(this.user).then(res => {
        localStorage.setItem("user", JSON.stringify(res.data.list));
        Toast(res.data.msg);
        if (res.data.code == 200) {
          this.$router.push("/index");
          return;
        }
      });
    }
  },
  data() {
    return {
      user: {
        phone: "",
        password: ""
      }
    };
  }
};
</script>

<style scoped lang="stylus">
@import '../../stylus/index.styl';
.title{
  position: fixed;
  top: 0;
  width: 100vw;
  background: $primary;
  color: $font-color5;
  line-height: $h5;
  text-align: center;
  font-size: $h2;
  z-index 1
  .right{
  position: fixed;
  top: 0;
  right .13rem

  color: $font-color5;
  line-height: $h5;
  text-align: center;
  font-size: $h2;
  
  z-index 2

  }
}
</style>


